.main {
  flex-grow: 1;
  background-color: rgba(255, 255, 255, 1);

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
  }
}

.main__slogan {
  height: 389px;
  color: rgba(255, 255, 255, 1);
  background-color: rgba(0, 0, 0, 1);
  background-image: url("../img/intro/welcome-background.jpg");
  background-size: 320px 389px;
  background-repeat: no-repeat;

  @media (min-width: @tablet-width) {
    height: 518px;
    background-image: url("../img/intro/welcome-background--desktop.jpg");
    background-size: 768px 518px;
    background-repeat: no-repeat;
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/intro/welcome-background--desktop.jpg");
    background-size: 1200px 518px;
    background-repeat: no-repeat;
  }
}

.main__attractions {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  justify-content: center;
  align-content: center;
  list-style-type: none;
}

.main__search-hotel {
  display: flex;
  flex-direction: column;
  padding: 39px 18px 49px 18px;

  @media (min-width: @tablet-width) {
    padding: 61px 155px 58px 155px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 66px;
  }
}

.main__location {
  display: flex;
}

.main__introduce {
  height: 214px;
  color: rgba(255, 255, 255, 1);
  background-color: rgba(0, 0, 0, 1);
  background-image: url("../img/intro/sedona-background.jpg");
  background-size: 320px 214px;
  background-repeat: no-repeat;
  margin: 0;

  @media (min-width: @tablet-width) {
    height: 290px;
    background-image: url("../img/intro/sedona-background--tablet.jpg");
    background-size: 768px 290px;
    background-repeat: no-repeat;
    margin-top: 56px;
  }

  @media (min-width: @desktop-width) {
    height: 518px;
    background-image: url("../img/intro/sedona-background--desktop.jpg");
    background-size: 1200px 518px;
    background-repeat: no-repeat;
  }
}

.main__introduction {
  display: flex;
  flex-direction: column;
  padding-top: 36px;
  padding-bottom: 28px;

  @media (min-width: @tablet-width) {
    align-self: center;
    padding: 64px 166px 102px 167px;
  }

  @media (min-width: @desktop-width) {
    padding: 115px 380px 67px 380px;
  }
}

.main__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
